.CTA {
  text-decoration: none;
  padding: 10px 15px;
  background-color: darkorange;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
  width: 90%;
}

.CTA:hover {
  background-color: #0056b3;
}

html {
  scroll-behavior: smooth;
}










#caruselDiv {
  width: 100%;
  height: auto;
  border-radius: 16px;


}


#carusel {
  scroll-behavior: auto;
  /* למנוע שינוי נקודת האפס */
  overflow-x: auto;

}




#innerCarusel {
  display: inline-flex;
  align-items: stretch;

}



.item {

  /* backdrop-filter: blur(6px); */
  color: #ffffff;
  font-family: 'Segoe UI', sans-serif;
  border-radius: 12px;
  height: 400px;
  flex-shrink: 0;
  transition: transform 0.3s ease, background 0.3s ease;


}

.item img {
  width: 100%;
  height: 400px;
  border-radius: 10px;
  object-fit: cover;
  user-select: none;
  /* מונע בחירת טקסט */
  pointer-events: none;
}

#carusel::-webkit-scrollbar {
  display: none;
}


@media only screen and (max-width: 768px) {
  .item {
    height: 350px;
    max-width: 450px;
  }

  .item img {
    height: 350px;
    max-width: 450px;
  }
  

}





